.fallback-spinner{min-height:calc(100vh - 460px)}.fallback-spinner .bg-spin{display:flex;width:100%;height:100%;flex:1;position:absolute;z-index:99;right:0;left:0;top:0;bottom:0;background-color:rgba(241,241,241,0.5)}.fallback-spinner .loading{position:absolute;left:calc(50% - 35px);top:50%;width:55px;height:55px;border-radius:50%;-webkit-box-sizing:border-box;box-sizing:border-box;border:3px solid transparent;z-index:100}.fallback-spinner .loading.component-loader{margin-top:-60px}.fallback-spinner .loading .effect-1,.fallback-spinner .loading .effect-2{position:absolute;width:100%;height:100%;border:3px solid transparent;border-left:3px solid #e03e0e;border-radius:50%;-webkit-box-sizing:border-box;box-sizing:border-box}.fallback-spinner .loading .effect-1{animation:rotate 1s ease infinite}.fallback-spinner .loading .effect-2{animation:rotateOpacity 1s ease infinite 0.1s}.fallback-spinner .loading .effect-3{width:100%;height:100%;border:3px solid transparent;border-left:3px solid #e03e0e;-webkit-animation:rotateOpacity 1s ease infinite 0.2s;animation:rotateOpacity 1s ease infinite 0.2s;border-radius:50%;-webkit-box-sizing:border-box;box-sizing:border-box}.fallback-spinner .loading .loading .effects{transition:all 0.3s ease}.fallback-spinner .fallback-logo{position:absolute;left:calc(50%);top:30%;transform:translate(-50%)}@keyframes rotate{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(1turn);transform:rotate(1turn)}}@keyframes rotateOpacity{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg);opacity:0.1}100%{-webkit-transform:rotate(1turn);transform:rotate(1turn);opacity:1}}
